import axios from 'axios';
import {
  filmsLoadingActionCreator,
  filmsSuccessActionCreator,
  setPagesActionCreator,
  filmsErrorActionCreator,
} from './filmsSearchActions';

export const API_KEY = 'bcf1e9109b9838d979a3eda6f6128d7e';

const fetchFilms = (query = '', page) => dispatch => {
  const censurePattern = /^f$|^hj$/i;
  if (query.match(censurePattern)) {
    query = '';
  }
  dispatch(filmsLoadingActionCreator());
  // https://api.themoviedb.org/3/discover/movie?api_key=bcf1e9109b9838d979a3eda6f6128d7e&sort_by=popularity.desc&include_adult=false&page=1&with_genres=53
  axios
    .get(
      // `https://api.themoviedb.org/3/discover/movie?api_key=${API_KEY}&sort_by=popularity.desc&query=${query}&include_adult=false&page=${page}&with_genres=${genre}`
      `https://api.themoviedb.org/3/search/movie?api_key=${API_KEY}&query=${query}&page=${page}&include_adult=false`,
    )
    .then(response => {
      dispatch(filmsSuccessActionCreator(response.data.results));
      dispatch(setPagesActionCreator(response.data.total_pages));
    })
    .catch(error => {
      if (error) {
        console.log(error);
        dispatch(filmsErrorActionCreator(error));
      }
    });
};
export default fetchFilms;
